import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/firestore'

const fn = store => ({
    increment: state => ({ count: state.count + 1 }),
    decrement: state => ({ count: state.count - 1 }),
    isUserEmail: (states, email) => {
        let db = firebase.firestore()
        db.collection('tokyoislands').doc('people').collection('users').where('email', '==', email).get().then(query => {
            console.log(query.docs[0].exist())
            console.log(query.docs[0].data())
        }).catch(err => {
            console.log(err)
        })
    },
    emailAuthentication: (states, email, password) => {
        firebase.auth().signInWithEmailAndPassword(email, password).catch(function(error) {
            let errorCode = error.code
            let errorMessage = error.message
            console.log(errorCode)
            console.log(errorMessage)
            console.log(error)
        })
        //store.setState(states => ({
        //    s: {
        //        ...states.s,
        //        login: true
        //    },
        //}))
    },
    auth: state => ({
    })
});

export default fn;
